export class DeserializableObject {
    jsonKey: string;
    
    deserialize(json: Object, jsonKey: string): any {
        this.jsonKey = jsonKey;

        for(let jsonAttrName in json) {
            let found = false;
            for(let objAttrName in this) {
                if(this.unifyAttrName(objAttrName) === this.unifyAttrName(jsonAttrName)) {
                    this[objAttrName] = json[jsonAttrName];
                    found = true;
                    break;
                }
            }
            if(!found)
                this[jsonAttrName] = json[jsonAttrName];
        }
        
        return this;
    }
    
    private unifyAttrName(attrName: string): string {
        return attrName.toLocaleLowerCase().replace('_', '').replace('-', '');
    }
}