import { Component, OnInit, Input, OnChanges, SimpleChanges } from '@angular/core';
import { Item } from '@app/item/model/item';

@Component( {
    selector: 'itemDetail',
    templateUrl: './itemDetail.component.html'
} )
export class ItemDetailComponent implements OnInit, OnChanges {
    
    @Input() item: Item;
    itemAttrs: string[];
    
    constructor() { }

    ngOnInit(): void {
//        console.log(Object.keys(this.item));
    }
    
    ngOnChanges(changes: SimpleChanges): void {
        if(changes.item.currentValue)
            this.itemAttrs = Object.getOwnPropertyNames(changes.item.currentValue);
        else
            this.itemAttrs = undefined;
    }

}
