import { Injectable, Inject, Optional, ReflectiveInjector, Injector } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

import { Item } from '@app/item/model/item';


@Injectable( { providedIn: 'root' } )
export class ItemService {
    private static items = new Map<string, Item>();

    constructor( private http: HttpClient ) {
    }

    private getJson(): Observable<any> {
        return this.http.get( "./assets/data/item.min.json" );
    }

    init(): Promise<any> {
        return new Promise((resolve, reject) => {
            this.getJson().subscribe(jData => {
                for ( let key in jData ) {
                    let item: Item = new Item().deserialize(jData[key], key);
                    ItemService.items.set( key, item );
                }
                resolve(true);
            });
          });
    }


    getByNameId( nameId: string ): Observable<Item> {
        if ( ItemService.items.size === 0 ) {
            this.init();
        }
        return new Observable<Item>(( observer ) => {
            observer.next( ItemService.items.get( nameId ) );
            observer.complete()
        } );
    }

    getAll( limit?: number ): Observable<Item[]> {
        return new Observable<Item[]>(( observer ) => {
            if ( ItemService.items.size === 0 ) {
                this.init().then(jData => {
                    let items: Item[];
                    if ( limit ) {
                        items = Array.from( ItemService.items.values()).slice(0, limit);
                    } else {
                        items = Array.from( ItemService.items.values() );
                    }
                    observer.next( items );
                    observer.complete()
                });
            }
        } );
    }
}